body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0 auto;
  padding: 0;
  background-color: green;
}

.App {
  background-color: gray;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  max-width: 1240px;
  min-width: 320px;
}

.question {
  margin: 0 auto;
  padding: 35px;
  background-color: gray;
  font-size: 20px;
}

.answers {
  background-color: gray;
  margin: 0 auto;
  padding: 20px;
}

.answers p {
  margin: 5px 0;
  padding: 15px;
  border: 1px solid black;
  border-radius: 15px;
  font-size: 20px;
  cursor: pointer;
}

.active {
  color: green;
  border: 1px solid gold !important;
}

.correct {
  background-color: green;
}

.wrong {
  background-color: red;
}

.App button {
  margin: 15px auto;
  padding: 15px;
  width: 30%;
  border-radius: 15px;
  cursor: pointer;
}

.App button:focus {
  outline: none; 
}

